<template>
  <div id="announce_detail">
    <new-layout>
      <template slot="top">
        <announce-header>{{ detail.title }}</announce-header>
      </template>
      <template slot="center">
        <div class="detail_con">
          <div class="origin-date">
            <span>发布时间：{{ timeStamp(detail.publishTime) }}</span>
          </div>

          <div class="desc">
            <div class="banner">
             <img v-lazy="getImgURL(detail.thumbnailUrl.pelativePath)" v-if="detail.thumbnailUrl!=null"/>
              <img v-lazy="getImgURL(detail.thumbnailUrl)" v-if="detail.thumbnailUrl==null"/>

            </div>
            <div class="con" v-html="detail.content">detail.content</div>
          </div>


          <p class="attachment">附件下载</p>
          <li v-for="(item,index) in detail.attachmentFiles" :key="index" class="attachmentli">
            <a :href="URLPATH+/file/+item.pelativePath">{{item.name}}</a>
          </li>
          <!--文章推荐-->
          <new-recommend></new-recommend>
        </div>
      </template>
    </new-layout>
  </div>
</template>

<script>
    export default {
      name:"detail",
      data(){
        return{
          descPic: require('../../assets/imgs/announce/detail_banner.png'),
          detail:[],
          lujin: 'http://218.4.57.14:19103',
          URLPATH:''
        }
      },

      /**
       * 监听路由变化，重新渲染页面
       */
      watch:{
        async $route(to, from){
          if(to.query.id !== from.query.id){
            let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/message/detail?id=' + to.query.id);
            this.detail = res.result.message;
          }
        }
      },
      methods: {
        getImgURL1(imgURL) {
          let IMG_ROOT = Dev.API_ROOT;
          IMG_ROOT = IMG_ROOT.replace('"', '');
          if(!imgURL){
            return  IMG_ROOT + '/file/' + imgURL;
          } else {
            return  IMG_ROOT + '/static/imgs/error.png';
          }
        }
      },
      async created(){
        let that = this;
        let id = this.$route.query.id;
        let res = await this.$http.get(this.$HOST + '/open/health/message/center/app/message/detail?id=' + id);

        if(res.result){
          that.detail = res.result.message;
        }
        this.URLPATH = process.env.API_ROOT

       }
    }
</script>

<style scoped lang="scss">
  $greyColor: #8D8D8D;
  $fontSize: 27px;

  #announce_detail{
    background: #FFF;

    .detail_con{
      width: 93%;
      margin: auto;

      .origin-date{
        padding: 20px 0;
        span{
          color: $greyColor;
          font-size: $fontSize;
          padding-right: 30px;
        }
      }

      .desc{
        .banner{
          width: 100%;
          height: 430px;
        }
        .con{
          font-size: 30px;
          padding-top: 20px;
        }
      }
    }
  }
  .attachment{
    font-size: 28px;
    color: #ff4444;
    margin: 10px 0px 10px 0px;
    font-weight: bold;
  }
  .attachmentli{
    line-height: 50px;
  }
</style>



